html {
  font-size: 100%;
  box-sizing: border-box;
}

*, *::before, *::after {
  box-sizing: inherit;
}

body {
  width: 100%;
  overflow-x: hidden;
  margin: 0;
  padding: 0;
  font-family: var(--font-poppins);
}

ul {
  list-style-type: none;
}

@media (max-width: 63.9375em) {
  .hide-for-mobile {
    display: none;
  }
}

@media (min-width: 64em) {
  .hide-for-desktop {
    display: none;
  }
}

:root {
  --cream: #fffbf8;
  --dark: #333333;
}

:root {
  --font-poppins: "Poppins", sans-serif;
}

.container {
  max-width: 68.75rem;
  margin: 0 auto;
  padding: 0 1rem;
}
@media (min-width: 64em) {
  .container {
    padding: 0;
  }
}

.flex {
  display: flex;
  flex-direction: row;
}
.flex--j-c {
  justify-content: center;
}
.flex--j-sb {
  justify-content: space-between;
}
.flex--a-c {
  align-items: center;
}

h1,
h2,
h3 {
  margin-top: 0;
}

a,
a:visited,
a:active {
  text-decoration: none;
}

.header__navbar {
  height: 5rem;
}
.header__logo {
  position: relative;
  z-index: 15;
}
@media (min-width: 64em) {
  .header__nav-links {
    width: 40vw;
    display: flex;
    justify-content: space-between;
  }
}
.header__nav-item {
  color: inherit;
  position: relative;
}
.header__nav-item::after {
  content: "";
  position: absolute;
  margin: auto;
  bottom: -0.1875rem;
  left: 0;
  height: 0.125rem;
  width: 0;
  background-color: var(--cream);
  transition: width 0.3s ease-in-out;
}
.header__nav-item:hover::after {
  width: 100%;
}
.header__nav-menu-icon-wrapper {
  height: 2rem;
  width: 2rem;
  cursor: pointer;
}
@media (min-width: 64em) {
  .header__nav-menu-icon-wrapper {
    display: none;
  }
}
.header__nav-menu-icon {
  position: relative;
  z-index: 10;
  width: 1.6rem;
  height: 0.15rem;
  background-color: var(--cream);
  border-radius: 5px;
  transition: background-color 0.3s linear;
}
.header__nav-menu-icon.active:nth-of-type(1) {
  background-color: transparent;
}
.header__nav-menu-icon::before {
  content: "";
  position: absolute;
  top: -0.4375rem;
  width: inherit;
  height: inherit;
  background-color: var(--cream);
  border-radius: inherit;
  transition: transform 0.3s linear, top 0.3s linear;
}
.header__nav-menu-icon.active::before {
  background-color: var(--cream);
  transform: rotate(45deg);
  top: 0;
}
.header__nav-menu-icon::after {
  content: "";
  position: absolute;
  top: 0.4375rem;
  width: inherit;
  height: inherit;
  background-color: var(--cream);
  border-radius: inherit;
  transition: transform 0.3s linear, top 0.3s linear;
}
.header__nav-menu-icon.active::after {
  top: 0;
  transform: rotate(-45deg);
}
.header__mobile-nav-menu {
  position: absolute;
  top: 0;
  right: 0;
  left: 0;
  bottom: 0;
  padding: 10rem 6rem 10rem 0;
  opacity: 0;
  z-index: -1;
  visibility: hidden;
  transition: all 0.3s;
  background-color: rgba(0, 0, 0, 0.4);
}
.header__mobile-nav-menu-wrapper {
  height: 100%;
  flex-direction: column;
  align-items: flex-end;
  transform: skew(-14deg) translateX(-18%);
}
.header__mobile-nav-menu.active {
  opacity: 1;
  z-index: 5;
  visibility: visible;
}
.header__mobile-nav-menu::before {
  content: "";
  position: absolute;
  background-color: var(--dark);
  top: 0;
  bottom: 0;
  right: -0.9375rem;
  left: 0;
  transform-origin: 0 0;
  transform: skew(-14deg) translateX(-110%);
  transition: transform 0.2s ease-in-out;
}
.header__mobile-nav-menu.active::before {
  transform: skew(-14deg) translateX(0%);
}
.header__mobile-nav-item {
  position: relative;
  color: var(--cream);
  font-size: 1.875rem;
  visibility: hidden;
  opacity: 0;
  transform: translateY(0.625rem);
}
.header__mobile-nav-item:nth-child(1) {
  transition: all 0.3s 175ms;
}
.header__mobile-nav-item:nth-child(2) {
  transition: all 0.3s 225ms;
}
.header__mobile-nav-item:nth-child(3) {
  transition: all 0.3s 275ms;
}
.header__mobile-nav-item:nth-child(4) {
  transition: all 0.3s 325ms;
}
.header__mobile-nav-item:nth-child(5) {
  transition: all 0.3s 375ms;
}
.header__mobile-nav-item:nth-child(6) {
  transition: all 0.3s 425ms;
}
.header__mobile-nav-item.active {
  transform: translateY(0) skew(14deg);
  visibility: visible;
  opacity: 1;
}

.header {
  overflow-x: hidden;
  color: var(--cream);
  height: 100vh;
  width: 100vw;
  background-color: #1f2222;
}